import React, {useEffect} from "react";
import {Button , Form, Input} from "antd";
import {useDispatch, useSelector} from "react-redux";
import UserOutlined from "@ant-design/icons/lib/icons/UserOutlined";
import LockOutlined from "@ant-design/icons/lib/icons/LockOutlined";
import {userSignIn} from "../appRedux/actions/Auth";
import IntlMessages from "util/IntlMessages";
const FormItem = Form.Item;

const SignIn = (props) => {
  const dispatch = useDispatch();
  const token = useSelector(({auth}) => auth.token);

  const onFinishFailed = errorInfo => {
    console.log('Failed:', errorInfo);
  };

  const onFinish = values => {
    console.log(values)
    console.log("finish",values)
    dispatch(userSignIn(values));
  };

  useEffect(() => {
    if (token !== null) {
      props.history.push('/');
    }
  }, [token, props.history]);

  return (
    <div className="gx-login-container">
    <div className="gx-login-content">
      <div className="gx-login-header gx-text-center">
        <h1 className="gx-login-title"> <IntlMessages id="app.userAuth.signIn"/> </h1>
      </div>
      <Form
        initialValues={{ remember: true }}
        name="basic"
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        className="gx-signin-form gx-form-row0">
        <FormItem  initialValue="admin" rules={[{ required: true, message: 'Please input your Username!' }]} name="username">
            <Input prefix={<UserOutlined style={{color: 'rgba(0,0,0,.25)'}}/>}  placeholder="Username"/>
        </FormItem>
        <FormItem  initialValue="admin0123" rules= {[{required: true, message: 'Please input your Password!'}]}  name="password">
            <Input prefix={<LockOutlined  style={{color: 'rgba(0,0,0,.25)'}}/>} type="password" placeholder="Password"/>
        </FormItem>
        <Form.Item>
          <Button type="primary" className="gx-mb-0" htmlType="submit">
            <IntlMessages id="app.userAuth.signIn"/>
          </Button>
        </Form.Item>
        <span className="gx-text-light gx-fs-sm"> demo user email: 'demo@example.com' and password: 'demo#123'</span>
      </Form>
    </div>
  </div>
  );
};

export default SignIn;
